<template>
  <img alt="dh" src="../assets/pro.jpeg">
  <Titulo texto="Geek Facts!" />
  <!-- <button @click="consumirApi">Consumir API</button> -->
  <div v-for="item in arrayBlog" :key="item.id">
    <router-link :to="`/blog/${item.id}`">
    {{ item.id }} - {{ item.title }}
    </router-link>
  </div>
</template>

<script>
import Titulo from '../components/Titulo'

export default {
  components: {
    Titulo
  },
  data(){
    return {
      arrayBlog: []
    }
  },
  methods: {
    async consumirApi(){
      try {
        const data= await fetch('https://jsonplaceholder.typicode.com/posts')
        const array = await data.json()
        // console.log(array)
        this.arrayBlog = array;
      } catch (error) {
        console.log(error)
      }
    }
  },
  created(){ //recibe una funci√≥n
    this.consumirApi()
  }
}
</script>

